.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height:100vh;
}

.game-info {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width:100%;
  margin-top: 16px;
}

.move-button {
  width:200px;
  margin-bottom: 10px;
  padding: 10px 20px;
  background-color: #27ae60;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-weight: 200;
}
